@import url(https://fonts.googleapis.com/css?family=Blinker|Noto+Sans|Open+Sans|PT+Sans&display=swap);
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.App {
  text-align: center;
  font-size: 2rem;
  display: flex;
  flex-direction: column;
  align-content: center;
  background-image: url(/savings/static/media/floor-tile.ecff1f69.png);
  background-attachment: fixed;
}

.panelsInfo {
  color: white;
  display: flex;
  justify-content: center;
  align-content: center;
  margin: 2vh;
  font-family: 'Blinker', sans-serif;
}

.panelsInfo .popup {
  padding: 1.5vh 3.5vw;
  font-family: 'Blinker', sans-serif;
  background-color: rgba(47, 202, 189, 0.863);
  border: 7px solid white;
  box-shadow: 0 0 2px #000;
}

.panelsInfo .popup__cong {
  color: #003800;
  font-size: 2rem;
}

.panelsInfo .popup__close {
  padding: 1vh 2vh;
  font-size: 1.2rem;
  background-color: #33c0b4;
  color: white;
  border: 2px solid white;
  border-radius: 5px;
  cursor: pointer;
  -webkit-transition: .2s;
  transition: .2s;
}

.panelsInfo .popup__close:hover {
  background-color: #fff;
  border: 2px solid #0f413c;
  color: #1a5e58;
}

.panelsInfo__panel {
  font-size: 1.8rem;
  padding: 5vh 2vh;
  margin: 0 20px;
  background-color: rgba(127, 136, 141, 0.753);
  box-shadow: 0 0 2px 1px black;
  cursor: pointer;
  opacity: 0.9;
  -webkit-transition: .2s;
  transition: .2s;
  width: 90vw;
  background-image: url(/savings/static/media/money-bgc.74bc2e4f.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  text-shadow: 0px 0 5px black, 0px 0px 5px black,0px 0px 5px black;
}

.panelsInfo__panel:hover {
  opacity: 1;
}

.panelsInfo__panel span {
  display: block;
  margin: 10px;
}

@media (min-width: 720px) {
  .panelsInfo__panel {
    width: 60vw;
  }
}

.headerForm {
  background-color: #3f465e;
  padding: 2vh 1vw;
  color: white;
  max-width: 1200px;
  margin: 0 auto;
  border-radius: 0 0 10px 10px;
  box-shadow: 0 0 2px 2px gray;
}

.headerForm__slogan {
  font-size: 2rem;
  margin: 0.5;
  font-family: 'PT Sans', sans-serif;
}

@media (min-width: 720px) {
  .headerForm__slogan {
    font-size: 2.5rem;
  }
}

.headerForm .form {
  margin: auto 3vw;
  color: #b9b9b9;
  font-family: 'Open Sans', sans-serif;
}

@media (min-width: 500px) {
  .headerForm .form {
    margin: auto 5.5vw;
  }
}

@media (min-width: 1400px) {
  .headerForm .form {
    margin: auto 10vw;
  }
}

.headerForm .form__label {
  font-size: 1.5rem;
}

.headerForm .form__label__aim, .headerForm .form__label__price {
  color: white;
  font-size: 1.5rem;
  padding: 1vh 0 1vh 2vh;
  margin: 3vh;
  background-color: transparent;
  border: none;
  border-bottom: 2px solid #a7a7a7;
  border-radius: 3px;
  width: 75vw;
}

.headerForm .form__label__aim:focus, .headerForm .form__label__price:focus {
  border-color: white;
}

@media (min-width: 720px) {
  .headerForm .form__label__aim, .headerForm .form__label__price {
    font-size: 2rem;
    width: 50%;
  }
}

.headerForm .form__label__price {
  width: 30vw;
  max-width: 200px;
  text-align: center;
}

.headerForm .form__button {
  color: white;
  width: 65vw;
  max-width: 250px;
  height: 7vh;
  min-height: 50px;
  margin: 5vh;
  font-size: 1.4rem;
  border: 2px solid white;
  border-radius: 7px;
  background-color: rgba(0, 0, 0, 0.534);
  text-transform: uppercase;
  cursor: pointer;
  -webkit-transition: .3s;
  transition: .3s;
  opacity: 0.5;
}

.headerForm .form__button:hover {
  opacity: 1;
  background-color: transparent;
}

@media (min-width: 720px) {
  .headerForm .form__button {
    width: 40vw;
  }
}

.counters {
  font-family: 'PT Sans', sans-serif;
}

.counters .panelsCounters {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  margin: 0 auto 5vh;
  font-family: 'Noto Sans', sans-serif;
  font-size: 1.5rem;
}

@media (min-width: 1024px) {
  .counters .panelsCounters {
    flex-direction: row;
  }
}

.counters .panelsCounters__panel {
  width: 80vw;
  height: 20vh;
  background-color: rgba(247, 252, 255, 0.575);
  border: 1px solid #1d2335;
  margin: 3vh auto;
  padding: 0 2vh 2vh;
  -webkit-padding-after: 7vh;
}

@media (min-width: 720px) {
  .counters .panelsCounters__panel {
    width: 50vw;
  }
}

@media (min-width: 1024px) {
  .counters .panelsCounters__panel {
    width: 30vw;
    margin: 3vh;
  }
}

.counters__form {
  background-color: rgba(63, 70, 94, 0.884);
  padding: 2vh 1vw;
  color: white;
}

.counters__form__input {
  color: #fff;
  font-size: 1.5rem;
  width: 30%;
  max-width: 250px;
  border: none;
  border-bottom: 2px solid gray;
  background-color: transparent;
  text-align: center;
  margin-bottom: 1vh;
}

.counters__form__input:focus {
  border-color: white;
}

.counters__form__button {
  margin: 2vh 0 2vh 4vh;
  color: white;
  width: 30vw;
  max-width: 250px;
  height: 6vh;
  font-size: 1.2rem;
  border: 2px solid white;
  border-radius: 7px;
  background-color: rgba(54, 54, 54, 0.945);
  text-transform: uppercase;
  cursor: pointer;
  -webkit-transition: .3s;
  transition: .3s;
  opacity: 0.6;
}

.counters__form__button:hover {
  opacity: 1;
}

.footer {
  font-size: 1rem;
  font-family: 'PT Sans', sans-serif;
}
